	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>🌡9℃ ☁</strong></p>\n<p>今日は珍しく日が昇る前に起きました。\nふと思うことがあり、国語の教科書にあった『ちいちゃんのかげおくり』を紙芝居にしようと思いました。\n一・二の場面を作ったところで弟が起きて来ました。</p>\n<p>「紙芝居はやめて、地図を作ろうよ」</p>\n<p>弟が言ったので、僕は紙芝居を断念し、地図を作り始めました。</p>\n<p>「これ、なあに？」</p>\n<p>地図ができた頃、おもむろに弟が僕の机を開けて、引き出しの中にある黒い種を見つけました。</p>\n<p>「それは、秋からずっととっておいた、ヘチマの種だよ」</p>\n<p>僕が答えると、弟は言いました。</p>\n<p>「今日、種を蒔こうよ」</p>\n<p>僕は、横に首を振りました。</p>\n<p>「これは、春に植えるんだよ」</p>\n<p>それから、テレビを少し見て、いい時間になっていたことに気づき、両親を起こしました。\n今日は毛呂山町の福祉会館で、『ピノキオ』という演劇があるのです。</p>\n<p>演劇中、変装した役者が突然観客席を駆け回り、演劇はステージという枠の中でやるものという常識を持っていた僕はとても驚きました。</p>\n<p>演劇が終わったら、ジュースを口にしながら、親の車が来るのを待ちました。\nだいぶ遠いところに駐車していたようで、車が来るまでずいぶん時間が経ちました。</p>\n<p>その後、昼食を経て、午後は毛呂山台団地の自治会館へ向かいました。\nそこでは人形劇がやっていました。</p>\n<p>演者が何も喋っていないのに、人形の方から声が聞こえて来るので、とても驚きました。</p>\n<p>人形劇が終わると、参加記念にボールペンとケーキが配られました。\n僕にとっては初めてのボールペンです。\nこれまで水性サインペンと鉛筆、クーピー、クレヨン、あとは筆くらいしか使ったことがないので新鮮です。</p>\n<p>新鮮と言えば、夕方に床屋で髪を切ってもらいました。\n大変さっぱりしました。</p>","frontmatter":{"cover":null,"date":"1991-01-27T21:00:00+09:00","strDate":"1991/1/27","title":"日記"}}},"pathContext":{"path":"/ja/blog/1991/01/27-diary/","langKey":"ja"}}

